import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    height: 224,
    width: "100%"
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
}));

export default function VerticalTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <Tabs
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab label="แฟ้มที่แฟ้มที่แฟ้มที่แฟ้มที่แฟ้มที่แฟ้มที่แฟ้มที่แฟ้มที่ 1" {...a11yProps(0)} />
        <Tab label="แฟ้มที่ 2" {...a11yProps(1)} />
        <Tab label="แฟ้มที่ 3" {...a11yProps(2)} />
        <Tab label="แฟ้มที่ 4" {...a11yProps(3)} />
        <Tab label="แฟ้มที่ 5" {...a11yProps(4)} />
        <Tab label="แฟ้มที่ 6" {...a11yProps(5)} />
        <Tab label="แฟ้มที่ 7" {...a11yProps(6)} />
        <Tab label="แฟ้มที่ 8" {...a11yProps(7)} />
        <Tab label="แฟ้มที่ 9" {...a11yProps(8)} />
        <Tab label="แฟ้มที่ 10" {...a11yProps(9)} />
        <Tab label="แฟ้มที่ 11" {...a11yProps(10)} />
        <Tab label="แฟ้มที่ 12" {...a11yProps(11)} />
        <Tab label="แฟ้มที่ 13" {...a11yProps(12)} />
      </Tabs>
      <TabPanel value={value} index={0}>
        แฟ้มที่ 1
      </TabPanel>
      <TabPanel value={value} index={1}>
        แฟ้มที่ 2
      </TabPanel>
      <TabPanel value={value} index={2}>
        แฟ้มที่ 3
      </TabPanel>
      <TabPanel value={value} index={3}>
        แฟ้มที่ 4
      </TabPanel>
      <TabPanel value={value} index={4}>
        แฟ้มที่ 5
      </TabPanel>
      <TabPanel value={value} index={5}>
        แฟ้มที่ 6
      </TabPanel>
      <TabPanel value={value} index={6}>
        แฟ้มที่ 7
      </TabPanel>
      <TabPanel value={value} index={7}>
        แฟ้มที่ 8
      </TabPanel>
      <TabPanel value={value} index={8}>
        แฟ้มที่ 9
      </TabPanel>
      <TabPanel value={value} index={9}>
        แฟ้มที่ 10
      </TabPanel>
      <TabPanel value={value} index={10}>
        แฟ้มที่ 11
      </TabPanel>
      <TabPanel value={value} index={11}>
        แฟ้มที่ 12
      </TabPanel>
      <TabPanel value={value} index={12}>
        แฟ้มที่ 13
      </TabPanel>
    </div>
  );
}