import image1 from './images/about1.jpg';
import image2 from './images/about6.jpg';
import image3 from './images/about3.jpg';
import image4 from './images/about4.jpg';
import image5 from './images/about5.jpg';
import { ImageGridList } from '../ImageGridList';

import profile from ".././images/profile.jpg";
const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'transparent',
    '& > div': {
      maxWidth: 40,
      width: '100%',
      backgroundColor: '#006064',
    },
  },
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: 'none',
    color: '#006064',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(15),
    marginRight: theme.spacing(1),
    '&:focus': {
      opacity: 1,
    },
  },
}))((props) => <Tab disableRipple {...props} />)

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: "100%",
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
    width: "100px",
    height: "100px",
    borderRadius: "50%",
    border: ".5vh solid",
    borderColor: fade(theme.palette.warning.dark, 0.2),
  },
  tabs: {
    width: "100%",
  },
}));

export const ReviewCard = ({itemsCard}) => {
    const tileData = [
        {
           img: image1,
            title: 'Image',
            author: 'author',
            cols: 2,
          },
          {
          img: image2,
            title: 'Image',
            author: 'author',
            cols: 1,
          },{
              img: image3,
               title: 'Image',
               author: 'author',
               cols: 1,
             },{
              img: image4,
               title: 'Image',
               author: 'author',
               cols: 1,
             },{
              img: image5,
               title: 'Image',
               author: 'author',
               cols: 1,
             },
        ];
       
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [overall, setOverall] = React.useState(true);
  const [education, setEducation] = React.useState(false);
  const [work, setWork] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  const handleExpandClickOverall = () => {
    setOverall(!overall);
    setEducation(false);
    setWork(false);
  };
  const handleExpandClickEducation = () => {
    setEducation(!education);
    setOverall(false);
    setWork(false);
  };
  const handleExpandClickWork = () => {
    setWork(!work);
    setEducation(false);
    setOverall(false);
  };

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
    console.log(newValue);
    if(newValue===0){
      setOverall(!overall);
      setEducation(false);
      setWork(false);
    }
    if(newValue===1){
      setEducation(!education);
      setOverall(false);
      setWork(false);
    }
    if(newValue===2){
      setWork(!work);
      setEducation(false);
      setOverall(false);
    }
  };
  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <img src={profile} className={classes.avatar}></img>
          
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        
      title={<div><Typography paragraph>{itemsCard.cardTitle}</Typography></div>}
        subheader={<div>
          {itemsCard.cardDate}<p>ครูผู้ช่วย โรงเรียนภูเขียว สพม.30</p>
          </div>}
      />
      <ImageGridList className={classes.media} images={tileData}></ImageGridList>
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          {itemsCard.cardDetail}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <div className={classes.tabs}>
        <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
          <StyledTab label="Workflows" ></StyledTab>
          <StyledTab label="Datasets" />
          <StyledTab label="Connections" />
        </StyledTabs>
        <Typography className={classes.padding} />
      </div>
      </CardActions>
      <Collapse in={overall} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>ข้อมูลทัวไป</Typography>
          <Typography>
            Set aside off of the heat to let rest for 10 minutes, and then serve.
          </Typography>
        </CardContent>
      </Collapse>
      <Collapse in={education} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>ประวัติการศึกษา</Typography>
          <Typography>
            Set aside off of the heat to let rest for 10 minutes, and then serve.
          </Typography>
        </CardContent>
      </Collapse>
      <Collapse in={work} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>ประวัติการทำงาน</Typography>
          <Typography>
            Set aside off of the heat to let rest for 10 minutes, and then serve.
          </Typography>
        </CardContent>
      </Collapse>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>Method:</Typography>
          <Typography>
            Set aside off of the heat to let rest for 10 minutes, and then serve.
          </Typography>
        </CardContent>
      </Collapse>
    </Card>
  );
}